	import {
		mapState
	} from 'vuex'
	import {
		getHomeList,
		addHit
	} from '../../api/mainApi.js'
	import {
		appUpdate
	} from '../../api/userApi.js'
	import T from '@/utils/tips.js'
	import TabBar from '@/components/common/TabBar.vue'
	import SwiperDot from "@/components/common/SwiperDotByMain.vue"
	export default {
		data() {
			return {
				checkIndex: 0,
				homeList: {},
				banner: [], // 轮播图
				navs: [], // 导航nav
				advs: [], // 广告
				seles: [], // 精选
				indicatorDots: false,
				autoplay: true,
				interval: 3000,
				duration: 500,
				platform: 0,
				cur:0,
				listWidth:0,
				roleId:'',
				colorPattern: {
					'红': '255,0,0',
					'黄': '255,55,0',
					'蓝': '30,30,255',
					'紫': '200,50,248',
				},
        color: 'red'
			}
		},
		components: {
			TabBar,SwiperDot
		},
		onTabItemTap(e){
			uni.setStorageSync('pagePath','main')
		},
		onLoad() {
			uni.setStorageSync('pagePath','main')
			// 版本更新 （APP）





			// 设备样式兼容
			this.platform = uni.getStorageSync('platform');
		},
		onShow() {
			
			uni.hideLoading()
			// 获取首页banner
			this.getHomeList()
			// 判断用户类型
			this.assessUserType()
			
			
		},
		onPullDownRefresh() {
			//监听下拉刷新动作的执行方法，每次手动下拉刷新都会执行一次
			console.log('refresh');
			// 获取首页banner
			this.getHomeList()
			setTimeout(function() {
				uni.stopPullDownRefresh(); //停止下拉刷新动画
			}, 1000);
		},
		methods: {
			// 判断用户类型
			assessUserType(){
				// 设置底部tab样式
				this.roleId = uni.getStorageSync('roleId')
				if(this.roleId){
					if(this.roleId == '20002'){
						uni.setTabBarItem({
						  index: 1,
						  text: '代办',
						  iconPath: '/static/img/2.1.png',
						  selectedIconPath: '/static/img/2.2.png'
						})
						
					} else if(this.roleId == '20001') {
						uni.setTabBarItem({
						  index: 1,
						  text: '我要卖',
						  iconPath: '/static/img/4.1.png',
						  selectedIconPath: '/static/img/4.2.png'
						})
					}
				}
			},
			changeBanner(e) {
				this.cur = e.detail.current;
			},
			// 显示分类
			goClassify(){
				uni.navigateTo({
					url:'/pages/main/classify/classify'
				})
			},
			//去进货单
			goOrder(){
				uni.navigateTo({
					url:'/pages/order/order'
				})
			},
			// 更新版本
			updataApp() {

































































































































			},
			// 去广告页面
			goadSet(item) {
				addHit({
					id: item.id
				})
				if (item.type == 1) {
					uni.navigateTo({
						url: '/pages/common/webview/webview?url=' + item.url
					})
				} else if (item.type == 5) {
					uni.navigateTo({
						url: '/pages/order/goodsDetail/goodsDetail?shopId=1&goodsId=' + item.url
					})
				}
			},
			// 计算
			calcAttr(attr){
				let str = attr.substr(0,1)
				let color = this.colorPattern[str] || '230,230,230'
				return {
					color: 'rgba('+color+',1)',
					bg: 'rgba('+color+',0.5)'
				}
				
			},
			// nav 去搜索页面
			goSearchPage(name) {
				uni.navigateTo({
					url: '/pages/order/goodsList/goodsList?search=' + name
				})
			},
			// 去搜索页面
			goSearch() {
				uni.navigateTo({
					url: '/pages/main/search/search'
				})
			},
			// 获取首页banner
			getHomeList() {
				getHomeList({
					parentId: 1
				}).then((res) => {
					if (res.code == '1000') {
						this.homeList = res.data
						// this.homeList.list[3].list[1].goodsDetailRespList.forEach((item, index) => {
						// 	item.valueAddr = item.valueAddr.substring(0, 5)
						// })
						
						this.listWidth = uni.upx2px(this.homeList.list[3].list[1].goodsDetailRespList.length * 100) + 'px';
					}
				})
			},
			goNextPage(item) {

				addHit({
					id: item.id
				})

				if (item.type == 5) {
					uni.navigateTo({
						url: '/pages/order/goodsDetail/goodsDetail?shopId=' + item.shopId + '&goodsId=' + item.id
					});
				} else if (item.type == 1) {
					uni.navigateTo({
						url: '/pages/common/webview/webview?url=' + item.url
					})
				}
			},
			goGoodsDetail(shopId, goodsId) {
				uni.navigateTo({
					url: '/pages/order/goodsDetail/goodsDetail?shopId=' + shopId + '&goodsId=' + goodsId
				})
			},
			chooseColorValue(v){
				let obg = {}
				if(v.indexOf('黄') != '-1'){
					obg = {
						bgColor:'#FF5500',
						fontColor:'#FFF6F0'
					}
				}else if(v.indexOf('红') != '-1'){
					obg = {
						bgColor:'#FFF0F0',
						fontColor:'#FF0000'
					}
				}else if(v.indexOf('紫') != '-1'){
					obg = {
						bgColor:'#FBF0FF',
						fontColor:'#C42AFD'
					}
				}
				return obj;
			}
		}
	}
